      },
      queryValveCurrentData (){
        console.info("组织机构ID："+this.treeNodeInfo.treeNodeOrgId);

        let that = this;
        let ci_company_id ='';
        let ci_child_company_id ='';
        let ci_institute_id ='';
        let ci_station_id ='';
        let ci_community_uuid = '';
        if(this.pri_detail.treeNodeType !== undefined ){
          let count = this.pri_detail.treeNodeType.length ;
          let type = this.pri_detail.treeNodeType[count-1];

          if(type === "1"){
            ci_company_id = this.pri_detail.treeNodeIds[count-1] ;
          }else if(type === "2"){
            ci_child_company_id = this.pri_detail.treeNodeIds[count-1] ;
          }else if(type === "3"){
            ci_institute_id = this.pri_detail.treeNodeIds[count-1] ;
          }else if(type === "4"){
            ci_station_id = this.pri_detail.treeNodeIds[count-1] ;
          }
        }
        if(this.dataQuery.community !== undefined){
          ci_community_uuid =  this.dataQuery.community.ci_uuid
        }
        let _jsondata_ =   {
          perPageSize:this.queryParam.pageSize,
          currentPage:this.queryParam.currentPage,
          ci_community_name:this.ruleForm.ci_community_name,
          ci_province:this.ruleForm.ci_province,
          ci_company_id:ci_company_id,
          ci_child_company_id:ci_child_company_id,
          ci_institute_id:ci_institute_id,
          ci_station_id:ci_station_id,
          ci_community_uuid:ci_community_uuid,
          stationIds : this.stationIds,
        } ;
        if(this.sortOrderCol!==''){
          _jsondata_.orderby = this.sortOrderCol;
        }
        //console.info("json:"+JSON.stringify(_jsondata_));
        // const loading = this.$loading({
        //   lock: true,
        //   text: '查询中...',
        //   target: '.main',
        //   spinner: 'el-icon-loading',
        //   background: 'rgba(0, 0, 0, 0)'
        // });
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.residentialQuarters_query,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){
            // loading.close();
            that.tableData = res.data.rows;
            that.queryParam.totalRow = res.data.total;

          }else{
            console.info("查询失败");
            // loading.close();

            that.tableData = [];
            this.$alert(res.data.msg, '查询失败', {
              confirmButtonText: '确定',
              callback: action => {
                console.info('data:'+JSON.stringify(res.data));
                if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                  this.$router.replace({path:'/login'});
                }
              }
            });
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          // loading.close();
          that.tableData = [];
          this.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          // loading.close();

          that.tableData = [];
          this.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });

      },
      sortColumn(column) {
        console.log("已经排序项：" + JSON.stringify(column));
        let orderFlag = '';
        if (column.order != null) {
          if (column.order === "descending") {
            orderFlag = 'desc';
          } else if (column.order === "ascending") {
            orderFlag = 'asc';
          }
          console.info("属性名称："+ column.prop+",排序方式："+column.order);

          this.sortOrderCol = "vci." + column.prop + " " + orderFlag;
          console.info("用户选择排序列："+this.sortOrderCol);
          this.queryValveCurrentData ();
        }else{
          this.sortOrderCol = '';
        }
