          this.treeDataOptions[0] ="0";
          this.treeDataOptions[1] = this.communityForm.ci_company_id;
          if (this.communityForm.ci_child_company_id !== '' && this.communityForm.ci_institute_id !== '' && this.communityForm.ci_station_id !== '') {
            this.treeDataOptions[2] = this.communityForm.ci_child_company_id;
            this.treeDataOptions[3] = this.communityForm.ci_institute_id;
            this.treeDataOptions[4] = this.communityForm.ci_station_id;
          } else if (this.communityForm.ci_child_company_id === '' && this.communityForm.ci_institute_id !== '' && this.communityForm.ci_station_id !== '') {
            this.treeDataOptions[2] = this.communityForm.ci_institute_id;
            this.treeDataOptions[3] = this.communityForm.ci_station_id;
          } else if (this.communityForm.ci_child_company_id !== '' && this.communityForm.ci_institute_id === '' && this.communityForm.ci_station_id !== '') {
            this.treeDataOptions[2] = this.communityForm.ci_child_company_id;
            this.treeDataOptions[3] = this.communityForm.ci_station_id;
          } else {
            this.treeDataOptions[2] = this.communityForm.ci_station_id;
          }
        }
        this.communityForm.ci_province = row.ci_province;
        this.communityForm.ci_city = row.ci_city;
        this.communityForm.ci_country = row.ci_country;
        this.selectedOptions =[];
        this.selectedOptions[0] = this.communityForm.ci_province;
        this.selectedOptions[1] = this.communityForm.ci_city;
        this.selectedOptions[2] = this.communityForm.ci_country;
        this.communityForm.ci_longitude = row.ci_longitude;
        this.communityForm.ci_latitude = row.ci_latitude;
        this.communityForm.ci_build_area = row.ci_build_area;
        this.communityForm.ci_contact_name = row.ci_contact_name;
        this.communityForm.ci_contact_phone = row.ci_contact_phone;
        this.communityForm.ci_memo = row.ci_memo;
        this.communityForm.ci_uuid = row.ci_uuid;
        this.communityForm.flag = '2';
        this.communityDialogFormVisible = true;

      },
      handleDeleteRow(index, row) {
        console.log("行号："+index+",行内容：" + JSON.stringify(row) );
        this.communityForm.tbi_count = row.tbi_count;
        if(row.tbi_count !== 0 || row.tpui_count !== 0){
          this.$alert('当前要删除小区[<span style="color:red;font-size: 20px;">' + row.ci_community_name + '</span>]的楼栋或用户数量不为零，不可以删除', '删除提示', {
            dangerouslyUseHTMLString: true
          });
        }else {
          this.$confirm('请再次确认是否要删除[<span style="color:red;">' + row.ci_community_name + '</span>]该行记录?', '删除提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            dangerouslyUseHTMLString: true,
            closeOnClickModal: false,
            closeOnPressEscape: false,

          }).then(() => {
            let _jsondata_ =   {
              ci_uuid:row.ci_uuid,
            } ;
            var p1 = this.customGlobal.postHttpClient(
              this.customGlobal.serverUrl.residentialQuarters_delete,
              //this.customGlobal.serverUrl.baseURI,
              this.customGlobal.serverUrl.orgBaseURI,
              {"Content-Type": "application/x-www-form-urlencoded"},
              this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
            );
            p1.then((res) => {
              console.log("异步调用成功返回:"+JSON.stringify(res));
              if(res.data.status=='success'){
                this.$message({
                  type: 'success',
                  showClose: true,
                  duration:3000,
                  dangerouslyUseHTMLString: true,
                  message: '<span style="color:black;">基础信息 ['+row.ci_community_name+'] 删除成功 </span>'
                });
                this.reQueryValveCurrentData();
              }else{
                console.info("删除失败");
                this.$alert(res.data.msg, '删除失败', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.info('data:'+JSON.stringify(res.data));
                    if(res.data.msg==='请登录认证'){
                      this.$router.push({path:'/login'});
                    }
                  }
                });
              }
            },error=>{
              console.log("异步调用出错:"+JSON.stringify(error));
              this.$alert(JSON.stringify(error.message), '删除出错!', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            }).catch((res) => {
              console.log("异步调用失败返回:"+JSON.stringify(res));
              this.$alert(JSON.stringify(res.message), '删除出错', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            });
          }).catch((res) => {
            console.log("取消删除:"+JSON.stringify(res));
            this.$message({
              type: 'info',
              showClose: true,
              duration:3000,
              dangerouslyUseHTMLString: true,
              message: '已取消删除'
            });
          });
        }
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this;
            let _jsondata_ =   {
              ci_uuid:that.communityForm.ci_uuid,
              ci_company_id :that.communityForm.ci_company_id ,
              ci_child_company_id :that.communityForm.ci_child_company_id ,
              ci_institute_id :that.communityForm.ci_institute_id ,
              ci_station_id :that.communityForm.ci_station_id ,
              ci_community_name:that.communityForm.ci_community_name,
              ci_address:that.communityForm.ci_address,
              ci_province:that.communityForm.ci_province,
              ci_city:that.communityForm.ci_city,
              ci_country:that.communityForm.ci_country,
              ci_build_area:that.communityForm.ci_build_area,
              ci_build_type:that.communityForm.ci_build_type,
              ci_longitude:that.communityForm.ci_longitude,
              ci_latitude:that.communityForm.ci_latitude,
              ci_memo: that.communityForm.ci_memo,
              flag : that.communityForm.flag
            } ;
            const loading = this.$loading({
              lock: true,
              text: '提交中...',
              target: '.main',
              spinner: 'el-icon-loading',

              background: 'rgba(0, 0, 0, 0)'
            });
            if(that.communityForm.flag === '1'){
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.residentialQuarters_add,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.communityDialogFormVisible =false;
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.communityForm.ci_community_name+'] 新增成功 </span>'
                  });
                  this.queryValveCurrentData();
                }else{
                  console.info("新增失败");
                  loading.close();
                  this.$alert(res.data.msg, '新增失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }
              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '新增出错!', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '新增出错', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              });
              this.queryValveCurrentData();
            }else{
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.residentialQuarters_update,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.communityDialogFormVisible =false;
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.communityForm.ci_community_name+'] 修改成功 </span>'
                  });
                  this.queryValveCurrentData();
                }else{
                  console.info("修改失败");
                  loading.close();
                  this.$alert(res.data.msg, '修改失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }
              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '修改出错!', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '修改出错', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              });
              this.queryValveCurrentData();
            }

          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      removeTreeNode (node, data) {
        const parent = node.parent
        const children = parent.data.detail || parent.data
        const index = children.findIndex(d => d.id === data.id)
        children.splice(index, 1)
      },
      initTreeDataStatus(h, { node, data, store }) {

        if(JSON.stringify(this.treeDataStatus)==="{}"){
          return(
            <span style="font-size:14px;">
            <span>{data.caption }</span>
            </span>
        )
        }else{
          let find = false;
          for (let k of Object.keys(this.treeDataStatus)) {
            if(data.id===k){
              //console.info(k+",显示"+data.caption );
              return(
                <span >
                <span>{data.caption }</span>
                </span>
            )
              find = true;
              break;
            }else{
            }
          }
          if(find === false){
            this.removeTreeNode( node,data);
          }
        }
      },
      initOrgTemplateInfo(){
        const loading = this.$loading({
          lock: true,
          text: '查询中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postQueryOrganTemplate,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify({config:'all',username:this.customGlobal.userInfoExt.cui_login_id})})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){
            if( this.customGlobal.userInfoExt.cui_type !== '0' ){
              this.treeData = JSON.parse(res.data.organ).detail;
            }else{
              this.treeData =  [JSON.parse(res.data.organ)];
            }
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            loading.close();

          }else{
            console.info("查询失败");
            this.treeData = [ JSON.parse(res.data.organ) ];
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            loading.close();
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();

          this.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }
    }
  }
</script>

